* {
  user-select: none;
  overflow: hidden;
}
.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  background: radial-gradient(
    circle at 10.6% 22.1%,
    rgb(206, 18, 18) 0%,
    rgb(122, 21, 21) 100.7%
  );
  align-items: center;
  flex-direction: column;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.App-link {
  color: #61dafb;
}
.swipe {
  position: absolute;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.cardContainer {
  width: 300px;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 16px;
}

.card {
  max-width: 70vw;
  height: 350px;
  max-height: 50vh;
  width: 300px;
  background: antiquewhite;
  padding: 1rem;
  border-radius: 16px;
  font-size: 1.2rem;
  border: 8px solid cornflowerblue;
}

.card .content {
  color: black;
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  margin: 1.6rem;
  line-height: 1.4rem;
  min-height: 40%;
}

.card .punishment {
  font-family: "Dancing Script", cursive;
  text-transform: none;
  margin: 0.8rem;
  color: #a10303;
  font-weight: bold;
  font-size: 1.4rem;
}

.buttons {
  margin: 28px;
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 625px) {
  .buttons {
    flex-direction: column;
  }
}
.buttons button {
  background-color: #9198e5;
  border: none;
  border-radius: 5px;
  box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.1);
  color: #fff;
  flex-shrink: 0;
  font-size: 18px;
  font-weight: bolder;
  margin: 10px;
  padding: 10px;
  transition: 0.2s;
  width: 160px;
}

.buttons button:hover {
  cursor: pointer;
}

.pointer-none {
  pointer-events: none;
}

.hidden {
  display: none;
}

.show {
  display: block;
}

.reset-position {
  transform: translate3d(0px, 0px, 0px) rotate(0deg) !important;
}
