.stack {
  position: relative;
  width: 340px;
  height: 412px;
  overflow: visible;
}
.card-anim {
  max-width: 70vw;
  width: 300px;
  height: 350px;
  background: linear-gradient(
    70.8deg,
    rgb(31, 42, 147) 92.4% rgb(253, 70, 90) 24%
  );
  position: absolute;
  transform: translateY(0);
  animation: onTop 1.5s 0.2s ease infinite;
}
.card-anim.one {
  left: 0;
  top: 0;
  z-index: 2;
  animation-name: onTop;
}
.card-anim.two {
  left: 12px;
  top: 12px;
  z-index: 1;
  animation-name: onMiddle;
}
.card-anim.three {
  left: 24px;
  top: 24px;
  z-index: 0;
  animation-name: onBottom;
}

@keyframes onTop {
  5% {
    transform: translateY(-300px);
    z-index: 1;
    left: 12px;
    top: 12px;
  }
  10% {
    transform: translateY(0);
    z-index: 0;
    left: 24px;
    top: 24px;
  }
  40% {
    transform: translateY(0);
    z-index: 0;
    left: 24px;
    top: 24px;
  }
  50% {
    transform: translateY(0);
    z-index: 1;
    left: 12px;
    top: 12px;
  }
  70% {
    transform: translateY(0);
    z-index: 1;
    left: 12px;
    top: 12px;
  }
  80% {
    transform: translateY(0);
    z-index: 2;
    left: 0;
    top: 0;
  }
}
@keyframes onMiddle {
  10% {
    transform: translateY(0);
    z-index: 2;
    left: 0;
    top: 0;
  }
  40% {
    transform: translateY(0);
    z-index: 2;
    left: 0;
    top: 0;
  }
  45% {
    transform: translateY(-300px);
    z-index: 1;
    left: 12px;
    top: 12px;
  }
  50% {
    transform: translateY(0);
    z-index: 0;
    left: 24px;
    top: 24px;
  }
  70% {
    transform: translateY(0);
    z-index: 0;
    left: 24px;
    top: 24px;
  }
  80% {
    transform: translateY(0);
    z-index: 1;
    left: 12px;
    top: 12px;
  }
}
@keyframes onBottom {
  10% {
    transform: translateY(0);
    z-index: 0;
    left: 12px;
    top: 12px;
  }
  40% {
    transform: translateY(0);
    z-index: 1;
    left: 12px;
    top: 12px;
  }
  50% {
    transform: translateY(0);
    z-index: 2;
    left: 0;
    top: 0;
  }
  70% {
    transform: translateY(0);
    z-index: 2;
    left: 0;
    top: 0;
  }
  75% {
    transform: translateY(-300px);
    z-index: 1;
    left: 12px;
    top: 12px;
  }
  80% {
    transform: translateY(0);
    z-index: 0;
    left: 24px;
    top: 24px;
  }
}